html {
  font-size: 20px;
  scroll-behavior: smooth;

  @media (max-width: 1200px) {
    font-size: 18px;
  }
  @media (max-width: 992px) {
    font-size: 16px;
  }
  @media (max-width: 768px) {
    font-size: 14px;
  }
}

body {
  font-size: 1rem;
  font-family: Roboto, serif;
  background: #f3f3f3;
  min-width: 320px;
}

h1, h2, h3, h4, h5, h6, ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 20px;
}

input[type="submit"], button {
  border: none;
}

.btn {
  padding: .5rem 1rem;
  display: inline-block;
  border-radius: .5rem;
  color: #0e0e0e;
  background: #caaa48;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.btn:hover {
  transition: all 0.2s ease-in-out;
  background: #e7c879;
  box-shadow: 8px 8px 8px -5px rgba(34, 60, 80, 0.6);
}
.btn:disabled:hover {
  cursor: default;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.tinymodal-window {
  background: white;
  padding: 5px;
}

.modal-wrapper {
  position: relative;
  padding: 1rem;
  border-radius: 1rem;
  background: #7dccee;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-weight: 700;
}

.modal-title {
  text-transform: uppercase;
  font-size: 1.2rem;
  color: black ;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.modal-body {
  margin-top: .5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 2rem;
  border-top: 1px solid white;
  padding-top: .5rem;

.modal-body-img {
  max-width: 300px;
  img {
    max-width: 100%;
    height: 100%;
  }
}
.chars-list {
  margin-left: 1rem;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: column;
}
  li {
    color: deepskyblue;
    padding: 3px 0;
  }
  li span {
    color: black;
    font-size: .8rem;
  }
}

.parallax {
  background-image: url("../../../../img/bg/parallax-bg42.jpg");
  height: 100%;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  position: relative;
}
.parallax .mask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.5);
  z-index: 2;
}
