@import './form';

.content-items {
  padding: 2rem 0rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 1rem;
  column-gap: 1rem;
  z-index: 3;
}

.content-item {
  display: flex;
  padding: 1rem;
  flex-direction: column;
  width: 43%;
  height: auto;
  border-radius: 1rem;
  border: 1px solid grey;
  box-shadow: 13px 13px 10px -5px rgba(0, 0, 0, 0.7);
  background-color: #d9d9d9;
  color: #0e0e0e;
  z-index: 3;
  img {
    max-width: 100%;
    height: 100%;
    border-radius: .5rem;
    @media (max-width: 576px) {
      height: auto;
    }
  }
  @media (max-width: 768px) {
    width: 100%;
  }
}
.content-item-header {
  padding-left: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 2.5rem;
  border-bottom: 1px solid #caaa48;
  @media (max-width: 860px) {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
  }
  @media (max-width: 860px) {
    font-size: 1.2rem;
  }

}
.content-body {
  flex-grow: 1;
}
.content-item-img {
  position: relative;
  perspective: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 320px;
  border: 1px solid #f3f3f3;
  border-radius: .5rem;
}
.content-item-front, .content-item-back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s;
  backface-visibility: hidden;
  background-color: #fff;
  border-radius: .5rem;
}

.content-item-back {
  transform: rotateY(180deg);
}

.content-item-img:hover .content-item-front {
  transform: rotateY(180deg);
}
.content-item-img:hover .content-item-back {
  transform: rotateY(360deg);
}

.content-item-chars {
  padding: 1rem;
}

.content-item-chars-title {
  font-weight: 500;

}
.content-item-chars-list {
  margin-top: .5rem;
  margin-left: 1.2rem;
  li {
    color: #caaa48;
    font-size: .8rem;
  }
  li span {
    color: #333333;
  }
}

.content-item-chars-options {
  margin-top: .5rem;
  font-size: .7rem;
  font-style: italic;
  font-weight: 400;
}

.content-item-footer {
  border-top: 1px solid #caaa48;
  text-align: right;
  padding-top: .5rem;
}
