.header-wrap {
  position: relative;
  box-shadow: 0 8px 8px -5px rgba(255, 255, 255, 0.65);
  z-index: 3;
}
.header-wrap:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: .7;
  background: url("../../../../img/bg/1616551737_3-p-svetlo-zheltii-fon-32.jpg") top left no-repeat ;
  background-size: 100% 100%;
}
.header {
  position: relative;
  display: flex;
  padding-top: .5rem;
  padding-bottom: .5rem;
  align-items: center;
  column-gap: 2.5rem;
  justify-content: space-between;
  img {
    max-width: 100%;
    height: 100%;
  }

  @media (max-width: 680px) {
    position: relative;
    padding: 1.5rem;
    justify-content: center;
  }
  @media (max-width: 576px) {
    padding: 1rem;
  }

}
.header-logo-left {
  max-width: 15rem;

  @media (max-width: 1200px) {
    max-width: 12.5rem;
    img {
      max-width: 100%;
    }
  }
  @media (max-width: 680px) {
    position: absolute;
    z-index: 0;
    opacity: .3;
    left: 1rem;
  }

}
.header-img {
  @media (max-width: 1200px) {
    img {
      max-width: 100%;
    }
  }
  @media (max-width: 680px) {
    max-width: 20rem;
  }
}

.header-info {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  z-index: 5;
}
.header-email-btn, .header-order-btn {
  font-size: .8rem;
  text-align: center;
  min-width: 160px;

  @media (max-width: 768px) {
    min-width: 140px;
  }
  @media (max-width: 576px) {
    max-width: 125px;
    padding: .5rem .1rem;
    margin-left: 12px;
  }
}
.header-email-btn {
  position: relative;
  left: -25px;
  @media (max-width: 680px) {
    left: -10px;
  }
  @media (max-width: 576px) {
    left: 0;
  }
}
.header-order-btn {
  position: relative;
  right: -25px;
  @media (max-width: 680px) {
    right: -10px;
  }
  @media (max-width: 576px) {
    right: 0;
  }
}
.header-email-btn:before {
  content: '';
  display: inline-block;
  margin-right: .5rem;
  width: 1rem;
  height: 1rem;
  vertical-align: text-bottom;
  background: url("../../../../img/svg/email.svg") center no-repeat;
}
.header-order-btn:before {
  content: '';
  display: inline-block;
  margin-right: .5rem;
  width: 1rem;
  height: 1rem;
  vertical-align: text-bottom;
  background: url("../../../../img/svg/pen.svg") center no-repeat;
}
